import React from 'react';
import Modal from 'react-bootstrap/Modal';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import AdminServices from "services/api/AdminServices";
import FundConstituents from 'views/mobileView/components/molecules/Cards/FundConstituents';
import cssVar from 'views/mobileView/styles';
import { toast } from 'react-toastify';
import MintboxIcon from 'views/mobileView/components/atoms/MintboxIcon';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import { BsGraphUp } from 'react-icons/bs';
import ReadMoreText from 'views/mobileView/components/atoms/ReadMoreText';
import PerformanceChart from 'views/mobileView/components/molecules/Cards/PerformanceChart';
import { AiOutlineExclamationCircle } from 'react-icons/ai';

export default function RiskRecommendation(props) {

    const [mintbox, setMintbox] = React.useState({})
    const [fundConstituents, setFundConstituents] = React.useState([]);

    const STATE = props.location.state
    const MINTBOXID = props.location.state.MINTBOXID

    const [showPortfolioModal, setShowPortfolioModal] = React.useState(false);
    const [showHistoricalModal, setShowHistoricalModal] = React.useState(false)
    const [loading, setLoading] = React.useState(true);

    React.useEffect(async () => {
        await getMintboxById();
        await getMintboxConstituentsById();
        setLoading(false)
    }, [])

    const getMintboxById = async () => {
        let res = await AdminServices.getMintboxById(MINTBOXID).then(res => res)
        if (res.type) {
            setMintbox(JSON.parse(res.data)[0]["MintboxInfo"][0])
        } else {
            toast.error("Error")
        }
    }

    const getMintboxConstituentsById = async () => {
        let res = await AdminServices.getMintboxConstituentsById(MINTBOXID).then(res => res)
        if (res.type) {
            console.log("Get mintbox constituents API", res.data)
            setFundConstituents(res.data)
        } else {
            toast.error("Error")
        }
    }



    const PortfolioModal = () => {
        return (
            <Modal
                className="bottom-drawer"
                show={showPortfolioModal}
                onHide={() => setShowPortfolioModal(false)}
                keyboard={false}
            >
                <Modal.Body style={{ height: "80vh", overflow: "scroll" }} className='p-0'>
                    <FundConstituents fundConstituents={fundConstituents} />
                </Modal.Body>
                <Modal.Footer>
                    <button className="solidButton" onClick={() => setShowPortfolioModal(false)}>
                        Close
                    </button>
                </Modal.Footer>
            </Modal>
        )
    }

    const HistoricalModal = () => {
        return (
            <Modal
                className="bottom-drawer"
                show={showHistoricalModal}
                onHide={() => setShowHistoricalModal(false)}
                keyboard={false}
            >
                <Modal.Body style={{ maxHeight: "80vh", overflow: "scroll", padding: "0" }}>
                    <PerformanceChart fundCode={MINTBOXID} type={3} />
                </Modal.Body>
                <Modal.Footer>
                    <button className="outlineButton" onClick={() => setShowHistoricalModal(false)}>
                        Close
                    </button>
                </Modal.Footer>
            </Modal>
        )
    }

    if (STATE === undefined) {
        props.history.push('/Dashboard')
        return <></>
    }
    return (
        <div className='mainContainer'>
            <MainHeader
                header="Your recommended mintbox"
                navigation={props.history}
            />
            <div className="dataContainer">
                {
                    loading ?
                        <ActivityLoaderMintbox />
                        :
                        <>
                            <div className='header1dark' style={{ color: cssVar.getPropertyValue('--darkGreen'), marginTop: "6%", marginBottom: "4%" }}>
                                Recommended mintbox
                            </div>
                            <div style={{ padding: "4%", border: "1px solid " + cssVar.getPropertyValue("--light"), borderRadius: '8px', backgroundColor: cssVar.getPropertyValue("--lightest"), display: "flex", flexDirection: "column" }} onClick={()=>props.history.push('/mintbox-detail/'+MINTBOXID)}>
                                <div style={{ display: "flex", justifyContent: 'space-between', marginBottom: "4%" }}>
                                    <MintboxIcon risk={mintbox.RiskLevel} height='40px' width='40px' />
                                    <div>
                                        <div className="smallTextLight secondaryGreen" style={{ border: "1px solid " + cssVar.getPropertyValue('--secondaryGreen'), padding: "4px 8px", borderRadius: "4px", whiteSpace: "nowrap",backgroundColor:cssVar.getPropertyValue('--white') }} onClick={(e) => {e.stopPropagation();setShowPortfolioModal(true)}}>
                                            Show constituents
                                        </div>
                                    </div>
                                </div>
                                <div style={{ display: "flex", marginBottom: "3%", gap: '4%' }}>
                                    <div className="header2dark" style={{ flex: '1' }}>
                                        {mintbox.MintboxName}
                                    </div>
                                    <div>
                                        <div className="smallTextLight">5Y CAGR</div>
                                        <div className="header2dark" style={{ color: cssVar.getPropertyValue("--secondaryGreen") }}>{Number(mintbox.XIRR).toFixed(2) + " %"}</div>
                                    </div>
                                </div>
                                <div className='smallTextLight' style={{ whiteSpace: 'pre-line' }}>
                                    <ReadMoreText text={mintbox.About?.replaceAll(`{"\\n"}`, '\n\n')} />
                                </div>
                            </div>
                            {
                                fundConstituents.some(c=>c.AssetClassId === 3) ?
                                <div style={{ marginTop: '8%', padding: '4%', backgroundColor: cssVar.getPropertyValue('--lightest'), borderRadius: '8px' }}>
                                    <div style={{ display: 'flex', gap: '3%', alignItems: 'center',marginBottom:'2%' }}>
                                    <AiOutlineExclamationCircle size={16} style={{ color: cssVar.getPropertyValue('--internationalEquity') }} />
                                    <div className='header2dark'>Important note</div>
                                    </div>
                                    <div className='smallTextLight'>
                                    This mintbox’s constituents include US ETFs that are trading at premium due to SEBI’s restrictions. Once the restrictions are lifted, we will add US ETFs through rebalancing.
                                    </div>
                                </div>
                                :
                                <></>
                            }
                        </>
                }
            </div>
            <div style={{ position: "fixed", bottom: '70px', width: "100%",backgroundColor: cssVar.getPropertyValue('--white'), textAlign: "center" }}>
                <div className="bodyTextDark secondaryGreen" onClick={() => setShowHistoricalModal(true)} style={{ borderTop: "1px solid "+cssVar.getPropertyValue('--light'), padding: "3% 4%"}}>
                    Historical performance <BsGraphUp size={14} style={{ margin: "5px" }} />
                </div>
            </div>
            <div className='footer'>
                <button
                    className='solidButton'
                    onClick={() => props.history.push({
                        pathname: '/portfolio/confirm-invest',
                        state: {
                            fundData: { ...mintbox, MintboxId: Number(MINTBOXID), Constituents: fundConstituents },
                            type: 3
                        }
                    })}
                >
                    Start investing
                </button>
            </div>
            <PortfolioModal />
            <HistoricalModal />
        </div>
    )
}
